import React, { Component } from 'react';
import {Card, CardActions, CardHeader, CardMedia, CardTitle, CardText} from 'material-ui/Card';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import './Content.css';

var Comm = require('../../service/comm.js');

class Content extends Component {
   constructor(props) {
        super(props);
        this.state = {
        };
}

//<img  src="https://static.pexels.com/photos/479/landscape-nature-sunset-trees.jpg" alt="" />
    
      /*content= (<object  width="100%" height="100%"
                        data={this.props.src+'?autoplay=1&loop=1'}>
                    </object>);*/
                
  render() {
      let content="";
      if(this.props.type=="video"){
         content= (<object  width="100%" height="100%"
                        data={this.props.src+'?autoplay=1&loop=1'}>
                    </object>);
      }else{
          content=(<img style={{height:'100%'}}  src={this.props.src} alt="" />);
      }
    return (
      <div className="Content">
         <MuiThemeProvider >
         <CardMedia id="card%MediaID" className="fullscreen" style={{height:'100%'}}
                overlay={<CardTitle title={this.props.txt} subtitle={this.props.type} />}
          >
                {content}
        
            </CardMedia>
        </MuiThemeProvider >
      </div>
    );
  }
}


