
var io =require('socket.io-client') ;
var axios=require('axios') ;

class Comm{
    constructor(){
        this.comm={};
        this.comm.io={};
        this.comm.io.uuid=this.generateUUID();
        this.socket="";
        this.emitOnConnect=this.emitOnConnect.bind(this);

    }
    
    generateUUID(){
        var d = new Date().getTime();
        var uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c)      {
            var r = (d + Math.random()*16)%16 | 0;
            d = Math.floor(d/16);
            return (c=='x' ? r : (r&0x3|0x8)).toString(16);
        });
        return uuid;
     };
        
    toString(){
          return '';
    }
    
    emitOnConnect(message){
        console.log("message");
        console.log("socket");
        console.log(this.socket);
        console.log("this.comm.io.uuid");
        console.log(this.comm.io.uuid);
        this.socket.emit('data_comm',{'id':this.comm.io.uuid},function(test){
            console.log(test);
        });
    }
    
    socketConnection(slidToDisplayCallBack){
        this.socket = io.connect(process.env.SOCKET_URL);
        this.socket.on('connection', message=>{ this.emitOnConnect(message)});
        //this.socket.on('currentSlidEvent', message=>{ this.slidToDisplay(message)});
        this.socket.on('currentSlidEvent', message=>{ slidToDisplayCallBack(message)});
        
    }
    
   

}

module.exports = Comm;



